/* eslint-disable react-native/no-inline-styles */
/* eslint-disable react/no-unstable-nested-components */
import React from 'react';
import {Alert, Dimensions, StyleSheet, Linking, View} from 'react-native';
import {
  Button,
  IconButton,
  MD3Colors,
  Text,
  TextInput,
  useTheme,
  Modal,
  PaperProvider,
  Portal,
} from 'react-native-paper';
import {AnyObj, useAuthStore, Api} from '@smarttestpro/smart-common';
import {AuthConfiguration, authorize} from 'react-native-app-auth';
import jwtDecode from 'jwt-decode';
import {LoginAnimation} from '../lotties/LoginAnimation';
import {GoogleIcon} from '../icons/GoogleIcon';
import TermsScreen from './TermsScreen';
import PrivacyScreen from './PrivacyScreen';
import {FacebookIcon} from '../icons';

type LoginFormValues = {
  username: string;
  password: string;
};

const ssoConfig: AuthConfiguration = {
  // clientId: '<YOUR_CLIENT_ID>',
  // redirectUrl: '<YOUR_REDIRECT_URL>',
  // scopes: ['<YOUR_SCOPE_ARRAY>'],
  clientId: 'smarttest',
  redirectUrl: 'com.host.auth://oauth',
  clientSecret: 'lu85bexiwZMQwZb0hRMdSSBMQzrB9fYj',
  scopes: ['openid'],
  serviceConfiguration: {
    authorizationEndpoint:
      'https://auth.smarttest.pro/realms/smarttest/protocol/openid-connect/auth',
    tokenEndpoint:
      'https://auth.smarttest.pro/realms/smarttest/protocol/openid-connect/token',
    revocationEndpoint:
      'https://auth.smarttest.pro/realms/smarttest/protocol/openid-connect/logout',
  },
  additionalParameters: {
    prompt: 'login',
  },
};

const SignInScreen = () => {
  const {
    setAuth,
    setLoading,
    loading,
    getListSub,
    setDefaultSubId,
    setIsSubOwner,
    getUserRoles,
  } = useAuthStore();
  const theme = useTheme();

  const [username, setUsername] = React.useState('');
  const [password, setPassword] = React.useState('');
  const [loadingLogin, setLoadingLogin] = React.useState(false);
  const [visibleTerm, setVisibleTerm] = React.useState(false);
  const [visiblePrivacy, setVisiblePrivacy] = React.useState(false);
  const containerStyle = {backgroundColor: 'white', padding: 20};
  const showModalTerm = () => setVisibleTerm(true);
  const hideModalTerm = () => setVisibleTerm(false);
  const showModalPrivacy = () => setVisiblePrivacy(true);
  const hideModalPrivacy = () => setVisiblePrivacy(false);

  const processLoginWithToken = async (token: any) => {
    try {
      const userResult = await Api.profile.getUserInfo(token.access_token);

      const userInfoInToken: AnyObj = jwtDecode(token.access_token);
      if (userResult.status === 200) {
        console.log(Api.SubscriptionApi, 'Api.SubscriptionApi');
        const userDefaultSub = await Api.SubscriptionApi.getDefaultSub(
          token.access_token,
        );

        if (userDefaultSub.data) {
          setDefaultSubId(userDefaultSub.data.subId);
          setIsSubOwner(userDefaultSub.data.isSubOwner);

          await Promise.all([
            getListSub(Api.SubscriptionApi.getListSub, token.access_token),
            getUserRoles(
              Api.SubscriptionApi.getUserRolesInSub,
              token.access_token,
            ),
          ]);
        } else {
          setDefaultSubId('SMP');
        }

        setAuth({
          user: {
            fullName: userResult.data.name,
            sub: userResult.data.sub,
            sub_id: userResult.data.sub_id,
            email_verified: userResult.data.email_verified,
            name: userResult.data.name,
            preferred_username: userResult.data.preferred_username,
            given_name: userResult.data.given_name,
            family_name: userResult.data.family_name,
            email: userResult.data.email,
            roles: userInfoInToken.realm_access.roles as string[],
          },
          accessToken: token.access_token,
          refreshToken: token.refresh_token,
        });

        return Promise.resolve(true);
      }
    } catch (error) {
      setLoading('error');
      Alert.alert('Có lỗi xảy ra, vui lòng thử đăng nhập lại!');
    }
  };

  const signIn = async (config: AuthConfiguration) => {
    try {
      const authorizeResult = await authorize(config);
      // Alert.alert(authorizeResult.accessToken);
      await processLoginWithToken({
        access_token: authorizeResult.accessToken,
        refresh_token: authorizeResult.refreshToken,
      });
    } catch (error) {
      console.log('Error signIn:', error);
    }
  };

  // eslint-disable-next-line @typescript-eslint/no-shadow
  const onSubmit = async ({username, password}: LoginFormValues) => {
    setLoadingLogin(true);

    try {
      const result = await Api.auth.login({
        username,
        password,
      });

      if (result.status === 200) {
        await processLoginWithToken(result.data);
        return Promise.resolve(true);
      }
    } catch (error) {
      console.log(error);
      Alert.alert('Thông tin đăng nhập không chính xác, vui lòng thử lại !!');
    }

    setLoadingLogin(false);
  };

  return (
    <PaperProvider>
      <Portal>
        <View style={styles.container}>
          <LoginAnimation />
          <View
            style={{gap: 10, justifyContent: 'center', alignItems: 'center'}}>
            <Text
              style={{
                fontWeight: '500',
                fontSize: 28,
                color: theme.colors.primary,
              }}>
              Đăng nhập
            </Text>
            <Text>Mừng bạn trở lại</Text>
            <View style={{width: Dimensions.get('screen').width - 80}}>
              <TextInput
                style={{marginBottom: 8}}
                label="Tên đăng nhập"
                onChangeText={(text: string) =>
                  setUsername(text.trim().toLocaleLowerCase())
                }
                right={<TextInput.Icon icon="account" />}
              />
              <TextInput
                label="Mật khẩu"
                style={{marginBottom: 8}}
                onChangeText={(text: string) => setPassword(text)}
                secureTextEntry
                right={<TextInput.Icon icon="eye" />}
              />
            </View>
            <Button
              loading={loadingLogin}
              mode="contained"
              style={{width: Dimensions.get('screen').width - 80}}
              onPress={() => {
                if (username.length === 0) {
                  Alert.alert('Vui lòng nhập tên đăng nhập');
                  return;
                } else if (password.length === 0) {
                  Alert.alert('Vui lòng nhập mật khẩu');
                  return;
                }

                onSubmit({username, password});
              }}>
              Đăng nhập
            </Button>
            <Text style={{marginTop: 8, marginBottom: 0}}>
              Hoặc đăng nhập với
            </Text>
            <View style={{display: 'flex', flexDirection: 'row'}}>
              <IconButton
                size={28}
                loading={loading === 'loading' || loading === 'success'}
                mode="contained-tonal"
                icon={({size}: {size: number; color: string}) => {
                  return (
                    <View style={{width: size, height: size}}>
                      <GoogleIcon />
                    </View>
                  );
                }}
                onPress={() => {
                  signIn(ssoConfig);
                }}
              />
              {/* <IconButton
            size={28}
            loading={loading === 'loading' || loading === 'success'}
            mode="contained-tonal"
            icon={({size}: {size: number; color: string}) => {
              return (
                <View style={{width: size, height: size}}>
                  <FacebookIcon />
                </View>
              );
            }}
            onPress={() => {
              signIn(ssoConfig);
            }}
          /> */}
            </View>
          </View>
          <View
            style={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'space-between',
            }}>
            <Text
              style={{color: 'blue', margin: 20}}
              onPress={() => showModalPrivacy()}>
              Quyền riêng tư
            </Text>
            <Text
              style={{color: 'blue', margin: 20}}
              onPress={() => showModalTerm()}>
              Các điều khoản
            </Text>
          </View>

          <Modal
            visible={visibleTerm}
            onDismiss={hideModalTerm}
            contentContainerStyle={containerStyle}>
            <TermsScreen />
            <Button onPress={() => hideModalTerm()}>Đồng ý</Button>
          </Modal>
          <Modal
            visible={visiblePrivacy}
            onDismiss={hideModalPrivacy}
            contentContainerStyle={containerStyle}>
            <PrivacyScreen />
            <Button onPress={() => hideModalPrivacy()}>Đồng ý</Button>
          </Modal>
        </View>
      </Portal>
    </PaperProvider>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#fff',
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  welcomeHeadline: {
    color: MD3Colors.primary20,
  },
  welcomeText: {
    padding: 16,
    paddingBottom: 32,
  },
});

export default SignInScreen;
